* {
  box-sizing: border-box;
  /* overflow-x:hidden; */
  /* border:2px solid red; */
}

html {
  font-size: 14px;
}

body {
  font-size: 1rem;
  margin: 0;
  padding: 0;
  font-family: "David Libre", serif;
  overflow-x: hidden;
}

h1 {
  font-size: 6rem;
  margin: 0;
  font-family: "David Libre", serif;
  font-weight: 700;
  color:#333;
  direction: rtl;
}

h2 {
  font-size: 3rem;
  margin: 0;
  direction: rtl;
  font-family: "David Libre", serif;
  font-weight: 700;
  color:#333;
}

h3 {
  font-size: 2rem;
  margin: 0;
  font-family: "David Libre", serif;
  font-weight: 700;
  color:#333;
  direction: rtl;
}

h4 {
  margin: 0;
  font-size: 1.3rem;
  color:#333;  
  direction: rtl;
  font-family: "David Libre", serif;
  font-weight: 700;

}

p {
  font-size: 1.5rem;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
  font-family: "David Libre", serif;
  color:#333;
  direction: rtl;
}

a {
  font-size: 1.8rem;
  font-family: "David Libre", serif;
  color:#333;
  direction: rtl;
  text-decoration: none;
  /* so there will be no underline */
}

label{
  font-size: 1.5rem;
  font-family: "David Libre", serif;
  color:#333;
  direction: rtl;
  white-space: nowrap;
}

select{
  font-size: 1.5rem;
  direction: rtl;
  font-family: "David Libre", serif;
  color:#333;
}

input{
  direction: rtl;
  font-family: "David Libre", serif;
  font-size: 1.5rem;
}

textarea{
  direction: rtl;
  font-family: "David Libre", serif;
  font-size: 1.5rem;

}

button{
  font-family: "David Libre", serif;
  font-size: 1.2rem;
}

table th{
  font-size: 1.5rem;
}

@media (min-width: 320px) and (max-width: 480px) {

  html {
      font-size: 7px !important;
  }
  
  h1{
      font-size: 4rem !important;
  }
  
  h2{
    font-size: 3.5rem !important;
  }

  h3{
      font-size: 3rem !important;
  }

  h4{
    font-size: 2rem !important;
  }

  p{
      font-size: 2.5rem !important;
  }

  a{
      font-size: 2.5rem !important;
  }
}

@media (min-width: 481px) and (max-width: 768px) {
  html {
      font-size: 9px !important;
  }

  h1{
      font-size: 4rem !important;
  }

  h3{
      font-size: 2.5rem !important;
  }

  p{
      font-size: 2rem !important;
  }

  input{
    font-size: 2rem !important;
  }

  label{
    font-size: 2rem !important;
  }

  select{
    font-size: 2rem !important;
  }

  textarea{
    font-size: 2rem !important;
  }

}

@media (min-width: 769px) and (max-width: 1024px) {
  html {
      font-size: 11px !important;
  }
  
  h3{
      font-size: 2.5rem !important;
  }

  p{
      font-size: 2rem !important;
  }

  input{
    font-size: 2rem !important;
  }

  label{
    font-size: 2rem !important;
  }

  select{
    font-size: 2rem !important;
  }

  textarea{
    font-size: 2rem !important;
  }
}

/* this is the media query for peter-engineers tablets */